<template>
    <div class="user">
        <!-- <el-empty v-if="tableData = undefined" description="暂无用户"></el-empty> -->
        <el-table
            :data="pm"
            stripe
            style="width: 100%">
            <el-table-column
            align="center"
            prop="id"
            label="ID"
            width="180">
            </el-table-column>
            <el-table-column
            align="center"
            prop="password"
            label="密码">
            </el-table-column>
            <el-table-column label="操作" align="center">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)"><i class="el-icon-edit"></i>编辑</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)"><i class="el-icon-delete"></i>删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-sizes="[6]"
        :page-size="6"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
        </el-pagination>
    </div>
  </template>
  
  <script>
  
  export default {
    data(){
        return{
            tdp: [],
            // tableData:[[]],
            pm:[],
            page:1,
            total:null,

        }
    },
    methods:{
        handleEdit(index, row) {
            console.log(index, row);
        },
        handleDelete(index, row) {
            console.log(index, row);
            this.pm.splice(index,1);
            this.$api.getdeluser({id : row.id}).then(res=>{
              console.log(res.data.info);
            })
            this.apidate();
            // this.getd()
        },
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.page=val;
            sessionStorage.setItem('page',val)
            this.apidate()
        },
        // getdat(){
        //     for(let i=0;i<2;i++){
        //     for(let j=i*6;j<i*6+6;j++){
        //         if(this.tdp[j] != undefined){
        //             this.tableData[i].push(this.tdp[j]);
        //         }
        //     }
        // }
        // }
        // getd(){
        //     for(let i=0;i<2;i++){
        //     for(let j=i*6;j<i*6+6;j++){
        //         // if(this.tdp[j] != undefined){
        //             // this.tableData[i].push(this.tdp[j])
        //             this.p.push(this.tdp[j])
        //     }
        // }
        // }
        // getd(){
        //   let ak=[[],[]];
        // for(let i=0;i<2;i++){
        //   for(let j=i*6;j<i*6+6;j++){
        //     if(this.tdp[j] != undefined){
        //       ak[i].push(this.tdp[j]);
        //     }
        //   }
        // }
        // this.pm=ak[this.page-1]
        // console.log(this.pm);
        // },
        apidate(){
          this.$api.getuser({page:this.page}).then(res=>{
            // console.log(res.data.data);
            this.pm=res.data.data
            this.total=res.data.total
          })
        }
    },
    mounted(){
        if(sessionStorage.getItem('page')){
            this.page=Number(sessionStorage.getItem('page'));
        }
      // this.getd()
      this.apidate()
    },
    created(){
        // this.getd()
    }
  }
  </script>
  
  <style lang="less" scoped>
    .user{
        margin-top: 70px;
        margin-right: 10px;
    }
  </style>
  